import $ from 'jquery'
/**
 * user
 * @param user
 */
export function init(user) {
    render(user && user.mobile, user && user.shoppingCart)
}

/**
 * @param mobile
 * @param cart - cartuser,
 */
export function render(mobile, cart) {
    var $signin = $('#signin')
    var $account = $('#account')
    if (mobile) {
        $signin.hide()
        $account.show().html(mobile.substr(0, 3) + '****' + mobile.substr(7, 4))
    } else {
        $signin.show()
        $account.hide()
    }
}


